.card__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 90%;
  max-width: 1400px;
  margin: 40px auto 0px auto;
}

.card__box {
  background: #132e47;
  border-radius: 10px;

  width: 90%;
  max-width: 300px;
  margin: 12px;
  padding: 15px 0px 0px 12px;
}

.card__heading {
  font-size: 26px;
  text-align: center;
}

.card__box__large {
  background: #132e47;
  border-radius: 10px;

  width: 90%;
  max-width: 600px;
  margin: 12px;
  padding: 15px 0px 0px 12px;
}

.event__container {
  padding: 10px 15px;
}

.log__header {
  display: flex;
  justify-content: space-between;
}

.log__header__left {
  font-size: 27px;
  font-weight: 900;
}

.stats__box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #1f4979;
  line-height: 60px;
  color: #fff;
  flex-wrap: wrap;
}
.stat__icon {
  font-size: 30px;
  color: #3994ca;
}

.theadDark thead[class*="ant-table-thead"] th {
  background-color: #f3f3f3;
  font-size: 12pt;
}

.event__card__container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.log__header {
  display: flex;
  justify-content: space-between;
}

.log__header__left {
  font-size: 27px;
