  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #1f4979;
  line-height: 60px;
  color: #fff;
  flex-wrap: wrap;
}
.stat__icon {
  font-size: 30px;
  color: #3994ca;
}

.theadDark thead[class*="ant-table-thead"] th {
  background-color: #f3f3f3;
  font-size: 12pt;
}

*{padding:0;margin:0;box-sizing:border-box}.event_container{display:flex;width:100%;height:230px;background:#fff;width:600px;max-width:750px;border-radius:2px;box-shadow:0 2px 2px 0 rgba(0,0,0,0.25),0 8px 8px 0 rgba(0,0,0,0.15);margin:20px 0}.event_container .event_bg{width:40%;height:100%;background:#333;background-size:cover;background-position:center;background-repeat:no-repeat;border-top-left-radius:3px;border-bottom-left-radius:3px}.event_container .event_info{width:60%;height:100%;padding:10px 20px}.event_container .event_info .event_title{display:flex;width:100%;height:50px;align-items:center}.event_container .event_info .event_title h4{font-size:26px;font-family:"Quicksand", sans-serif;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.event_container .event_info .event_desc{width:100%;height:calc(100% - 100px)}.event_container .event_info .event_desc p{font-size:16px;font-weight:500;color:#565861;overflow:hidden;text-overflow:ellipsis}.event_container .event_info .event_footer{display:flex;width:100%;height:50px;align-items:center;justify-content:space-between}.event_container .event_info .event_footer .event_date p{font-size:15px;font-weight:600;color:#333}.event_container .event_info .event_footer .event_more{display:flex;width:auto;height:100%;align-items:center}.event_container .event_info .event_footer .event_more a.btn_more{display:flex;width:auto;height:40px;align-items:center;padding:0 15px;text-decoration:none;color:#5f5ffc;font-size:16px;font-weight:600;border-radius:2px;will-change:background;transition:background 0.3s, color 0.2s ease-in}.event_container .event_info .event_footer .event_more a.btn_more i.material-icons{font-size:18px;font-weight:500;padding:0 2px}.event_container .event_info .event_footer .event_more a.btn_more:hover{background:#5f5ffc;color:#fff}@media screen and (max-width: 575px){.event_container{width:90%;height:480px;background:#fff;flex-direction:column;margin:10px auto;max-width:300px;min-width:300px}.event_container .event_bg{width:100%;height:250px;min-height:250px;border-top-right-radius:3px;border-bottom-left-radius:0}.event_container .event_info{width:100%;height:230px}}

::-webkit-scrollbar {
  width: 5px;
  height: 4px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffa500;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ff8c00;
}
